import { Component, OnInit } from '@angular/core';
import { RestService } from '../service/rest.service';
import { Users } from '../model/users';
import { NgForm } from '@angular/forms';
import {newDetail} from '../model/newDetail.model';

@Component({
  selector: 'app-immunization',
  templateUrl: './immunization.component.html',
  styleUrls: ['./immunization.component.css']
})
export class ImmunizationComponent implements OnInit {
  allData: Users[];
  addedItem:newDetail;
  fname:any;
  constructor(private service: RestService) {}
  
  ngOnInit(): void {
     this.getAllData();
    //  console.log(JSON.stringify(data)); 
  }
  getAllData() {
    this.service.getAllData().subscribe((response)=>{
      this.allData = response;
    }
    );
   }

  onSubmit(form:NgForm){
    const value = form.value;
    const newData = new newDetail(value.name, value.desc, value.webr);
  }

  Search(){
    if(this.fname == ''){
      this.ngOnInit();
    }else{
      this.allData = this.allData.filter(res=>{
        console.log(res.name.toLowerCase().charAt(0));
        console.log(this.fname.toLowerCase().charAt(0));
        if(res.name.toLowerCase().charAt(0) == this.fname.toLowerCase().charAt(0)){

          return res;
        }
      })
    }
  }
   


}
